@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat-medium.woff2") format("woff2"), url("../fonts/montserrat-medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat-semiBold.woff2") format("woff2"), url("../fonts/montserrat-semiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat-regular.woff2") format("woff2"), url("../fonts/montserrat-regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap; }

*,
*::before,
*::after {
  box-sizing: inherit; }

img {
  max-width: 100%;
  height: auto;
  display: block; }

input, textarea, select, button {
  font: inherit; }

a {
  text-decoration: none;
  color: inherit; }

svg {
  fill: currentColor; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.container {
  max-width: 1920px;
  min-width: 320px;
  width: 1920px;
  margin: 0 auto;
  padding-right: 363px;
  padding-left: 363px; }
  @media (max-width: 1919px) {
    .container {
      width: 100%;
      padding: 0 20px; }
      .container--header {
        padding: 0; } }
  @media (max-width: 767px) {
    .container {
      padding: 0 15px; }
      .container--header {
        padding: 0; } }

.page {
  height: 100%;
  box-sizing: border-box; }

.page__body {
  margin: 0 auto;
  padding: 0;
  min-height: 100%;
  min-width: 320px;
  display: grid;
  grid-template-rows: -webkit-min-content 1fr -webkit-min-content;
  grid-template-rows: min-content 1fr min-content;
  font-family: "Montserrat", Arial, sans-serif;
  font-weight: normal;
  font-style: normal;
  background-color: #ffffff;
  color: #2C2E3F;
  font-size: 16px;
  line-height: 1.2; }

.header {
  display: flex;
  padding-top: 40px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1); }
  @media (max-width: 1919px) {
    .header {
      padding-top: 0;
      justify-content: space-between; } }

.header__logo {
  flex: 0 0 117px;
  padding-left: 12px; }
  @media (max-width: 1919px) {
    .header__logo {
      padding: 18px 20px 14px 21px;
      display: block;
      flex: 0 0 85px; } }
  @media (max-width: 767px) {
    .header__logo {
      padding: 18px 20px 14px 17px; } }

.header__nav {
  flex: 0 0 770px; }
  @media (max-width: 1919px) {
    .header__nav {
      flex: 0 0 auto; } }

.header__tel {
  flex: 0 0 303px; }

.toggle {
  display: none; }
  @media (max-width: 1919px) {
    .toggle {
      display: block;
      width: 72px;
      height: 50px;
      padding: 10px 21px;
      background-color: #FE7865;
      border: 0; }
      .toggle::before {
        content: "";
        display: block;
        position: absolute;
        width: 22px;
        height: 1px;
        background-color: #ffffff;
        box-shadow: 0 7px #ffffff, 0 13px #ffffff;
        top: 17px;
        right: 25px; } }

.nav {
  position: relative; }

.nav__list {
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  min-width: 0;
  word-break: break-word;
  list-style: none;
  justify-content: flex-end; }
  @media (max-width: 1919px) {
    .nav__list {
      display: none; } }

.nav__item {
  margin-right: 40px; }
  .nav__item:last-child {
    margin-right: 0; }

.nav__link {
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: #141517; }

.tel {
  font-weight: normal;
  font-size: 18px;
  line-height: 27px;
  color: #141517;
  text-align: right;
  padding-right: 23px;
  display: flex;
  padding-left: 55px;
  text-decoration: underline; }
  .tel::before {
    content: "";
    display: block;
    position: static;
    width: 30px;
    height: 30px;
    background-image: url("../img/icon-phone.svg");
    background-repeat: no-repeat;
    margin-right: 6px; }
  @media (max-width: 1919px) {
    .tel {
      display: none; } }
